// https://observablehq.com/@shouts77/random-dots@380
export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer()).define(["md"], function(md){return(
md`# Random Dots`
)});
  main.variable(observer("chart")).define("chart", ["d3","w","h","n","randInt","margin","color","opacityValue"], function(d3,w,h,n,randInt,margin,color,opacityValue)
{
  
  const svg = d3.create("svg")
                .attr("viewBox", [0, 0, w, h]);

  const dataset = [];
  for (let i = 0; i < n; ++i) {
    let newNumber1 = randInt(1600);
    let newNumber2 = randInt(900);
    dataset.push([newNumber1, newNumber2]);
    };

  const xScale = d3.scaleLinear()
                   .domain([0, d3.max(dataset, function(d) { return d[0]; })])
                   .range([margin.left, w-margin.left]);

  const yScale = d3.scaleLinear()
           .domain([0, d3.max(dataset, function(d) { return d[1]; })])
           .range([h-margin.top, margin.bottom]); 

  const rScale = d3.scaleSqrt()
                   .domain([0, d3.max(dataset, function(d) { return d[1]; })])
                   .range([0, 30]);

  const oScale = d3.scaleSqrt()
                   .domain([0, d3.max(dataset, function(d) { return d[1]; })])
                   .range([0, 1]);

  svg.selectAll("circle")
     .data(dataset)
     .enter()
     .append("circle")
//     .transition()
//     .delay((d, i) => i / dataset.length * 1000)
//     .duration(1000)
     .attr("cx", d => xScale(d[0]))
     .attr("cy", d => yScale(d[1]))
     .attr("r", d => rScale(d[1]))
     .style("fill", (d, i)=>color(i))
     .style("opacity", opacityValue);


  return svg.node();
}
);
  main.variable(observer()).define(["md"], function(md){return(
md`## Reference
- **[Generators]** By Mike Bostock (https://observablehq.com/@observablehq/introduction-to-generators)
- **[Random Function]** Canvas Random Dots (https://codepen.io/pumax/pen/gObJeJq)`
)});
  main.variable(observer()).define(["md"], function(md){return(
md`## Appendix`
)});
  main.variable(observer("color")).define("color", ["d3","n"], function(d3,n){return(
d3.scaleOrdinal([0,n], ["#a6cee3","#1f78b4","#b2df8a","#33a02c","#fb9a99","#e31a1c","#fdbf6f","#ff7f00","#cab2d6","#6a3d9a","#ffff99","#b15928"])
)});
  main.variable(observer("opacityValue")).define("opacityValue", ["Promises"], async function*(Promises)
{
  while (true) {
    await Promises.delay(1000);
    yield Math.random();
  }
 }
);
  main.variable(observer("randInt")).define("randInt", function(){return(
function (min, max) {
    if (max === undefined) {
      max = min;
      min = 0;
    }
    return Math.random() * (max - min) + min | 0;
  }
)});
  main.variable(observer("n")).define("n", function(){return(
100
)});
  main.variable(observer("w")).define("w", function(){return(
1600
)});
  main.variable(observer("h")).define("h", function(){return(
900
)});
  main.variable(observer("margin")).define("margin", function(){return(
{top: 30, right: 0, bottom: 30, left: 40}
)});
  main.variable(observer("d3")).define("d3", ["require"], function(require){return(
require("d3@6")
)});
  return main;
}
